.tasks-item-container {
    display: flex;
    justify-content: space-between;
    width: 395px;
    border-bottom: 1px solid #E4E4E4;
    padding: 15px 0;
}

.tasks-item-desc {
    display: flex;
    align-items: center;
}

.tasks-item-desc span {
    font-family: 'SF UI Display';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 17px;
    color: #333333;
}

.tasks-item-num {
    margin-right: 10px;
    border: 1px solid #C4C4C4;
    border-radius: 50%;
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'SF UI Display';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 17px;
    color: #333333;
}

.inpt-edit {
    height: 25px;
    background: #F4F4F4;
    border: none;
    font-family: 'SF UI Display';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 17px;
    color: #333333;
    outline: none;
}

.sumTime {
    font-family: 'SF UI Display';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 17px;
    color: #999999;
    margin-top: 19px;
}
